


























































































































































import FolderApi from '@/apis/FolderApi';
import { WorkspaceFolderModel, WorkspaceObjectModel, WorkspaceObjectType } from '@/apis/models/WorkspaceObjectModel';
import WorkspaceObjectItem from '@/components/test_maker/WorkspaceObjectItem.vue';
import QuizUnitSelector from '@/components/test_maker/QuizUnitSelector.vue';
import { SelectionEventBus } from '@/libs/EventBus';
import Breadcrumb from '@/components/workspace/Breadcrumb.vue';
import Utils from '@/libs/utils';
import Vue, { PropType } from 'vue';
import QuizGroupApi from '@/apis/QuizGroupApi';
import { QuizGroupModel } from '@/apis/models/QuizGroupModel';
import { MemberModel } from '@/apis/models/MemberGroupModel';
import MemberUnitSelector from '@/components/member_group_maker/MemberUnitSelector.vue';

export default Vue.extend({
  name: 'WorkspaceObjectSelector',
  components: {
    WorkspaceObjectItem,
    Breadcrumb,
    QuizUnitSelector,
    MemberUnitSelector,
  },
  props: {
    localRootId: {
      type: String,
      required: true,
    },
    shareRootId: {
      type: String,
      required: true,
    },
    localRootName: {
      type: String,
      required: true,
    },
    allowSelectMany: {
      type: Boolean,
      default: false,
    },
    defaultSelection: {
      type: Array as () => Array<WorkspaceObjectModel>,
      default: () => [],
    },
    isQuizUnitOn: {
      type: Boolean,
      default: false,
    },
    isMemberUnitOn: {
      type: Boolean,
      default: false,
    },
    shareGroupHidden: {
      type: Boolean,
      default: false,
    },
    duplicatedStudent: {
      type: Object as PropType<MemberModel>,
      default: () => ({
        uuid: '',
        name: '',
      }),
    },
    memberList: {
      type: Array as () => Array<MemberModel>,
      default: () => [],
    },
    displaySelectAllToggle: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      selectModeOpen: true,
      WorkspaceObjectType,
      selection: [] as WorkspaceObjectModel[],
      selectionSet: new Set(),
      currentFolder: null as WorkspaceFolderModel | null,
      currentQuizFile: null as QuizGroupModel | null,
      isLoading: false,
      searchString: '',
      allSelected: false,
    };
  },
  computed: {
    allFiles(): WorkspaceObjectModel[] {
      return this.workspaceObjects.filter((object) => object.metadata.type !== WorkspaceObjectType.Folder);
    },
    workspaceObjects(): WorkspaceObjectModel[] {
      if (this.currentFolder && !this.isMemberUnitOn) {
        const collator = new Intl.Collator('en', { numeric: true, sensitivity: 'base' });
        const fileArr = this.currentFolder.children.filter((object) => object.metadata.type !== WorkspaceObjectType.Folder);
        const folderArr = this.currentFolder.children.filter((object) => object.metadata.type === WorkspaceObjectType.Folder);

        let softLinkArr = this.currentFolder.children.filter((object) => object.metadata.type === WorkspaceObjectType.SoftLink);
        softLinkArr = softLinkArr.filter((object) => object.sourceObject);
        // console.log('softLinkArr', softLinkArr);

        if (softLinkArr.length > 0) {
          return softLinkArr.sort((a, b) => collator.compare(a.sourceObject.metadata.name.toUpperCase().trim(), b.sourceObject.metadata.name.toUpperCase().trim()));
        }

        fileArr.sort((a, b) => collator.compare(a.metadata.name.toUpperCase().trim(), b.metadata.name.toUpperCase().trim()));
        folderArr.sort((a, b) => collator.compare(a.metadata.name.toUpperCase().trim(), b.metadata.name.toUpperCase().trim()));
        const sortedArr = [] as WorkspaceObjectModel[];
        folderArr.forEach((element) => {
          sortedArr.push(element);
        });
        fileArr.forEach((element) => {
          sortedArr.push(element);
        });
        return sortedArr;
      }
      return [];
    },
    searchList(): WorkspaceObjectModel[] {
      const result = [] as WorkspaceObjectModel[];
      this.workspaceObjects.forEach((element) => {
        const listName = element.metadata.name;
        const inputName = this.trimSearchString(this.searchString);
        const indexOf = listName.toUpperCase().indexOf(inputName.toUpperCase());
        if (indexOf >= 0) {
          result.push(element);
        }
      });
      return result;
    },
    filteredMethodList(): WorkspaceObjectModel[] {
      const inputName = this.trimSearchString(this.searchString);
      if (inputName !== '') {
        return this.searchList;
      }
      return this.workspaceObjects;
    },
    searchMemberList(): MemberModel[] {
      const result = [] as MemberModel[];
      this.memberList.forEach((element) => {
        const listName = element.name;

        const inputName = this.trimSearchString(this.searchString);
        const indexOf = listName.toUpperCase().indexOf(inputName.toUpperCase());
        if (indexOf >= 0) {
          result.push(element);
        }
      });
      return result;
    },
    filteredMemberList(): MemberModel[] {
      const inputName = this.trimSearchString(this.searchString);
      if (inputName !== '') {
        return this.searchMemberList;
      }
      return this.memberList;
    },
  },
  mounted() {
    if (this.defaultSelection) {
      this.selection = Utils.deepcopy(this.defaultSelection);
    }
    this.selection.forEach((sel) => {
      this.selectionSet.add(sel.uuid);
    });
    this.visitFolder(this.localRootId);
    if (this.duplicatedStudent) {
      this.searchString = this.duplicatedStudent.name;
    }
  },
  methods: {
    checkAllSelected() {
      if (this.allFiles.length > 0) {
        let noSelectCount = 0;
        for (let index = 0; index < this.allFiles.length; index += 1) { // 檢查
          const file = this.allFiles[index];
          const isSelected = this.selectionSet.has(file.uuid);
          if (!isSelected) {
            noSelectCount += 1;
          }
        }
        if (noSelectCount === 0) { // 全選
          this.allSelected = true;
        } else {
          this.allSelected = false;
        }
      }
    },
    async selectAll() {
      if (!this.allSelected) {
        for (let index = 0; index < this.allFiles.length; index += 1) {
          if (!this.selectionSet.has(this.allFiles[index].uuid)) {
            this.selection.push(this.allFiles[index]);
            this.selectionSet.add(this.allFiles[index].uuid);
          }
        }
      } else { // 如果已經在全選的狀態下再按一次全選，就會把此資料夾中的selected file從selection及selectionSet移除
        for (let index = 0; index < this.allFiles.length; index += 1) {
          const key = this.selection.findIndex((item) => item.uuid === this.allFiles[index].uuid);
          if (key !== -1) { // 確認此檔案夾中的file存在於selection中 就從selection中移除
            this.selection.splice(key, 1);
          }
          this.selectionSet.delete(this.allFiles[index].uuid); // selectionSet也要移除
        }
      }
      await this.$nextTick();
      SelectionEventBus.$emit('reset');
      this.checkAllSelected();
    },
    trimSearchString(string: string) { // 修掉輸入的空白
      return string.trim();
    },
    async visitFolder(folderId: string) {
      this.isLoading = true;
      try {
        this.currentQuizFile = null;
        this.currentFolder = await FolderApi.retrieve(folderId);
        this.checkAllSelected();
      } catch (error) {
        console.error(error);
        this.$notify({
          type: 'error',
          title: this.$t('錯誤').toString(),
          text: this.$t('讀取資料夾失敗，請稍後重試').toString(),
        });
      } finally {
        this.isLoading = false;
      }
    },
    async visitFile(fileId: string) {
      this.isLoading = true;
      try {
        if (this.isQuizUnitOn) {
          this.currentQuizFile = await QuizGroupApi.retrieve(fileId);
        }
      } catch (error) {
        console.error(error);
        this.$notify({
          type: 'error',
          title: this.$t('錯誤').toString(),
          text: this.$t('讀取資料夾失敗，請稍後重試').toString(),
        });
      } finally {
        this.isLoading = false;
      }
    },
    async toggleSelection(object: WorkspaceObjectModel) {
      const index = this.selection.findIndex((item) => item.uuid === object.uuid);

      if (index === -1) {
        if (!this.allowSelectMany) {
          this.selection = [];
          this.selectionSet = new Set();
        }
        this.selection.push(object);
        this.selectionSet.add(object.uuid);

        if (!this.allowSelectMany) {
          await this.$nextTick();
          SelectionEventBus.$emit('reset');
        }
      } else {
        this.selection.splice(index, 1);
        this.selectionSet.delete(object.uuid);
      }

      this.checkAllSelected();
    },
  },
});
