



















import { WorkspaceObjectModel } from '@/apis/models/WorkspaceObjectModel';
import Vue, { PropType } from 'vue';
import { QuizPreview } from '@/apis/models/QuizModel';

export default Vue.extend({
  components: {
  },
  props: {
    workspaceObject: {
      type: Object as PropType<QuizPreview>,
      required: true,
    },
    defaultSelected: {
      type: Boolean,
      required: true,
    },
    allowSelectMany: {
      type: Boolean,
      default: false,
    },
    selection: {
      type: Array as () => Array<WorkspaceObjectModel>,
      required: true,
    },
    selectModeOpen: {
      type: Boolean,
      default: false,
    },
    index: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      selected: false,
    };
  },
  computed: {
  },
  watch: {
  },
  methods: {
    clickItemToggle() { // only not for Folder include btn area
      if (this.selectModeOpen) {
        this.selected = !this.selected;
        this.$emit('toggle');
      }
    },
  },
});
