



























import { MemberModel } from '@/apis/models/MemberGroupModel';
import { WorkspaceObjectModel } from '@/apis/models/WorkspaceObjectModel';
import { SelectionEventBus } from '@/libs/EventBus';
import Vue, { PropType } from 'vue';

enum MemberRole{
  STUDENT = 'STUDENT',
  TEACHER = 'TEACHER',
  STUDENT_DUPLICATED = 'STUDENT_DUPLICATED',
  STUDENT_EXEMPTED = 'STUDENT_EXEMPTED'
}

export default Vue.extend({
  components: {
  },
  props: {
    member: {
      type: Object as PropType<MemberModel>,
      required: true,
    },
    defaultSelected: {
      type: Boolean,
      required: true,
    },
    selection: {
      type: Array as () => Array<WorkspaceObjectModel>,
      required: true,
    },
    selectModeOpen: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      MemberRole,
      selected: false,
    };
  },
  computed: {
  },
  watch: {
  },
  mounted() {
    this.selected = this.defaultSelected;
    SelectionEventBus.$on('reset', () => {
      this.selected = this.defaultSelected;
    });
  },
  methods: {
    clickItemToggle() {
      if (this.selectModeOpen) {
        this.selected = !this.selected;
        this.$emit('toggle');
      }
    },
  },
});
